<template>
  <div id="register">
    <head-vue :title="title" goBack="true"></head-vue>
    <div class="bar">
      <img src="../../assets/me/4g@3x.png" alt="">
      <img src="../../assets/me/connect@3x.png" alt="">
      <img src="../../assets/me/phone@3x.png" alt="">
    </div>
    <div class="login">
      <div class="loginformat">
        <input type="tel" placeholder="请输入手机号" v-model="phoneNumber" @input="phoneVolidate" maxlength="11">
      </div>
      <div class="loginformat">
        <input type="tel" placeholder="请输入验证码" v-model="phoneCode" @input="codeVolidate" maxlength="6">
      </div>
      <span @click="getCheckCode" :class="{disalbedClick: !coded}">{{codeText}}</span>
      <div class="box"></div>
    </div>
    <div class="next" @click="next" :class="{Click: buttoned}">
     完成
    </div>
  </div>
</template>
<script>
import head from '../../components/common/head'
import {
  bindMobilePhoneRequest,
  getMobilePhoneCodeRequest
} from '../../api/method'
import {
  getMobilePhoneCodeResponse,
  bindMobilePhoneResponse
} from '../../api/getData'
import { getStore } from '../../utils/index'

export default {
  data () {
    return {
      title: '绑定手机号',
      emailActive: false, // button disabled
      phoneNumber: '',
      phoneCode: '',
      coded: false,
      codeText: '获取验证码',
      buttoned: false
    }
  },
  components: {
    'head-vue': head
  },
  computed: {
  },
  mounted () {
    getMobilePhoneCodeResponse(this.NAVIGATOR, this)
    bindMobilePhoneResponse(this.NAVIGATOR, this)
    this.init_()
  },
  methods: {
    init_ () {
      if (this.$route.params.type === 'unbind') {
        this.title = '更换手机号'
      }
    },
    next () {
      if (!this.buttoned) {
        return false
      }
      // this.$router.push({name: 'registerNext', params: {email: this.emailVal}})
      bindMobilePhoneRequest(this.NAVIGATOR, this, this.phoneNumber, this.phoneCode)
      console.log(this.phoneNumber, this.phoneCode)
      if (this.$root.ISDEBUG) {
        window.bindMobilePhoneResponse(true)
        window.errorCodeResponse('0x802506')
      }
    },
    getCheckCode () {
      if (!this.coded) {
        return false
      }
      let localUserInfo = JSON.parse(getStore('Userinfo'))
      if (!localUserInfo) return false
      if (localUserInfo.mobile !== '' && localUserInfo.mobile !== undefined) {
        getMobilePhoneCodeRequest(this.NAVIGATOR, this, this.phoneNumber, 2)
      }
      if (this.$root.ISDEBUG) {
        window.getMobilePhoneCodeResponse(true)
      }
    },
    phoneVolidate () {
      if ((/^1[34578]\d{9}$/.test(this.phoneNumber))) {
        this.coded = true
        this.emailActive = true
      } else {
        this.coded = false
        this.emailActive = false
      }
    },
    codeVolidate () {
      if ((/^\d+$/.test(this.phoneCode)) && (/^1[364578]\d{9}$/.test(this.phoneNumber))) {
        this.buttoned = true
      } else {
        this.buttoned = false
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import "../../styles/mixin.less";
#register{
  .wh(100%, 100%);
  .fs;
  z-index: 999;
  color: #585858;
  background: white;
  .bar{
    .wh(100%, 118px);
    display: flex;
    justify-content: center;
    margin-top: 95px;
    img:first-child{
      .wh(118px, 118px);
      align-self: center;
    }
    img:nth-child(2){
      .wh(58px, 24px);
      align-self: center;
      margin: 0 38px;
    }
    img:last-child{
      .wh(118px, 118px);
      align-self: center;
    }
  }
  .login{
    margin-top: 10px;
    color: #303030;
    width: 703px;
    margin: 0 auto;
    .loginformat{
      .wh(100%, 124px);
      // display: flex;
      padding-top: 30px;
      border-bottom: .5px solid #c5c5c5;
      // margin-top: 10px;
    }
    input{
      flex: 1;
      .wh-l(100%, 74px);
    }
    :-ms-input-placeholder{
      color: #585858;
    }
    ::-webkit-input-placeholder{
      color: #585858;
    }
    position: relative;
    >span{
      diplay: block;
      position: absolute;
      right: 20px;
      font-size: 28px;
      .wh-l(182px, 72px);
      background: @themeColor;
      border-radius: 50px;
      bottom: 0px;
      color: #ffffff;
      z-index: 100;
    }
    .box{
      .wh(200px, 72px);
       position: absolute;
      right: 0px;
      z-index: 10;
      bottom: -5px;
      background: #ffffff;
    }
    .disalbedClick{
      background: #D6D6D6;
    }
  }
  .next{
    .wh-l(702px, 98px);
    border-radius: 50px;
    margin: 0 auto;
    font-size: 34px;
    margin-top: 67px;
    color: white;
    background: #D6D6D6;
  }
  .Click{
    background: @themeColor;
  }
}
</style>



